/****** Elad Shechter's RESET *******/
/*** box sizing border-box for all elements ***/
*,
*::before,
*::after {
  box-sizing: border-box;
}

a,
a:visited,
a:active {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
}

button {
  background-color: transparent;
  color: inherit;
  border-width: 0;
  padding: 0;
  cursor: pointer;
}

figure {
  margin: 0;
}

input::-moz-focus-inner {
  border: 0;
  padding: 0;
  margin: 0;
}

ul,
ol,
dd {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-size: inherit;
  font-weight: inherit;
}

p {
  margin: 0;
}

cite {
  font-style: normal;
}

fieldset {
  border-width: 0;
  padding: 0;
  margin: 0;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  font-family: var(--ff-primary);
  background: var(--clr-background-body-gradient);
  font-size: var(--fs-primary-410);
  padding: 2em;
}

.main__faq__heading {
  font-size: var(--fs-primary-550);
  text-transform: uppercase;
  font-weight: var(--fw-primary-semiBold);
  color: var(--clr-primary-text);
}
.main__faq__qa__item {
  color: var(--clr-dark-grayish-blue);
}
.main__faq__qa__item__question {
  transition: all 0.2s ease;
}
.main__faq__qa__item__answer {
  font-size: var(--fs-primary-400);
  line-height: 1.5;
}

@media screen and (max-width: 900px) {
  body {
    font-size: var(--fs-primary-390);
  }

  .main__faq__heading {
    text-align: center;
    font-size: var(--fs-primary-550);
  }
  .main__faq__qa__item__answer {
    font-size: var(--fs-primary-380);
    max-width: 40ch;
  }
}
.main {
  position: relative;
  display: flex;
  justify-content: space-between;
  max-width: 90rem;
  flex: 1;
  border-radius: 30px;
  background: url(../../../images/bg-pattern-desktop.svg) no-repeat;
  background-position: -1046px 63%;
  background-size: 110%;
  background-color: var(--clr-background-card);
  box-shadow: 0px 20px 29px 0px rgba(0, 0, 0, 0.5);
}
.main__box-image {
  position: absolute;
  top: 45%;
  left: -8%;
  width: 250px;
  z-index: 1;
}
.main__img {
  transform: translateX(-5em);
}
.main__img--container {
  flex: 1;
  display: flex;
  overflow: hidden;
  align-items: center;
  justify-content: center;
}
.main__faq {
  flex: 1;
  padding: 5em 5em 5em 1em;
}
.main__faq__heading {
  margin-bottom: 1em;
}
.main__faq__qa__item {
  margin-bottom: 1em;
  padding-bottom: 1em;
  border-bottom: 1px solid var(--clr-dividers);
}
.main__faq__qa__item__question--container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1em;
}
.main__faq__qa__item__question__dropdown {
  max-width: 3%;
  border-radius: 50%;
  cursor: pointer;
  margin-left: 5px;
  transition: all 0.3s ease;
}
.main__faq__qa__item__answer {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.5s ease;
}
.main .answer-active {
  max-height: 100px;
}
.main .rotate-dropdown {
  transform: rotate(180deg);
}
.main .question-active {
  font-weight: var(--fw-primary-semiBold);
  color: var(--clr-Vdark-grayish-blue);
}

@media screen and (max-width: 900px) {
  .main {
    margin-top: 10em;
    max-width: 25rem;
    flex-direction: column;
    background-position: center -110px;
    background-size: 70%;
  }
  .main__box-image {
    left: 18%;
    position: absolute;
    top: -6%;
    width: 100px;
    width: 90px;
  }
  .main__img {
    transform: translateX(0);
    max-width: 250px;
    margin-top: -112px;
  }
  .main__img--container {
    overflow: visible;
  }
  .main__faq {
    margin-top: 2.5em;
    flex: 1;
    padding: 1.5em;
  }
  .main__faq__qa__item__question__dropdown {
    max-width: 5%;
  }
  .main body {
    padding: 1em;
  }
}